import React from "react";
import { useData } from "./useData";
import { LoadSpinner } from "../../elements/CommonUIs";
import { PieWithTableAndLegend } from "./PieWithTableAndLegend";
import { Collapsible } from "./Collapsible";

const title = "COVID-19 ";

const ChartTitle = ({ title }) => <div className="chart-title">{title}</div>;

const SourceCredit = () => (
  <p className="footnote">
    
    <a href="https://sites.google.com/cdc.gov.tw/2019ncov/taiwan">
      
    </a>
  </p>
);

export const TaiwanOverviewPie = () => {
  const data = useData();
  if (!data) return <LoadSpinner />;
  // console.log(data);
  return (
    <>
      <ChartTitle title={title} />
      <PieWithTableAndLegend data={data} />
      <Collapsible id={title}>
        <p>
          COVID-19
        </p>
        <p>
          
          <a href="https://www.nytimes.com/interactive/2020/world/asia/china-coronavirus-contain.html">
            
          </a>
          
        </p>
        <SourceCredit />
      </Collapsible>
    </>
  );
};
